<template>
  <div class="main_navs">
    <Header></Header>
    <section class="prof">
      <div class="container">
        <h1 class="h1_main">
          <span>{{$t('tit1')}}</span><br>
            {{$t('tit2')}}
          <span>{{ $t('tit3') }}</span>
        </h1>
        <div class="row filter">
          <div class="col-xl-6 input_text_f">
            <div class="input_filt">
              <input type="text" :placeholder="$t('search')">
              <div class="icon_f">

               {{$t('search')}}

              </div>
            </div>
          </div>
          <div class="col-xl-3 filt_f">
            <model-select :options="options"
                          v-model="item"
                          :placeholder="$t('kasb2')">
            </model-select>
          </div>

          <div class="col-xl-3 filt_f1">
            <model-select :options="options"
                          v-model="item"
                          :placeholder="$t('salery')">
            </model-select>
          </div>
        </div>
        <div class="row">
          <div class="col-xl-6" v-for="item in $store.state.professions_web.results" :key="item">
            <div class="prof_card">
                <div>
                  <div class="prof_title mb-3">{{item.title}}</div>
                  <div class="prof_desc" v-html="item.workplace"></div>
                  <div class="prof_fa">
                    <div class="prof_price">{{$t('salery')}}: {{item.salary_range}}</div>
                    <div class="prof_link">
                      <router-link class="decoration" :to="`/professions/${item.id}`">{{$t('more')}}</router-link>
                    </div>
                  </div>
                </div>
            </div>
          </div>
        </div>
        <div class="pagenationss">
          <b-pagination
              v-model="page"
              :total-rows="total"
              :per-page="perPage"
              first-number
              last-number
              aria-controls="my-table"></b-pagination>
        </div>
      </div>
    </section>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../components/Header";
import Footer from "../components/Footer";
import {ModelSelect} from 'vue-search-select'

export default {
  components: {Footer, Header, ModelSelect},
  data() {
    return {
      page: 1,
      total: 2,
      perPage:1,
      options: [
      ],
      item: {
        value: '',
        text: ''
      },
    }
  },
  mounted() {
    this.$store.dispatch('getProfision',1)
  },
  watch: {
    page(page) {
      this.$store.dispatch('getProfision', page)
    }
  },
}
</script>
